import {React, SeamlessImmutable} from 'jimu-core';
import BaseWidgetSetting from './base-widget-setting';
import {AllWidgetSettingProps} from './props';
import JSONEditor from './json-editor';

export default class Setting extends BaseWidgetSetting<AllWidgetSettingProps<any>, any>{
  render(){
    return <div style={{height: '100%', paddingTop: '10px'}}>
      <JSONEditor json={this.props.config} width='100%' height='100%'
        onChange={json => this.props.onConfigChange(this.props.id, SeamlessImmutable(json))}/>
    </div>;
  }
}