import {React, ReactDOM} from 'jimu-core';
import AceEditor from 'react-ace';
import * as brace from 'brace';
import 'brace/mode/json';
import 'brace/theme/tomorrow';

interface Props{
  json: any;
  width?: string;
  height?: string;
  onChange: (json: any) => void
}
export default class JSONEditor extends React.PureComponent<Props>{
  onChange = (newValue) => {
    try{
      const newJson = JSON.parse(newValue);
      newJson._defaultstr = newValue;
      this.props.onChange(newJson);
    }catch(err){
      //console.warn('JSON Editor:  Unexpected token in JSON', err);
    }
  }
  render(){
    const defaultValue = this.props.json._defaultstr || JSON.stringify(this.props.json);
    return  <AceEditor
              mode="json"
              theme="tomorrow"
              onChange={this.onChange}
              // debounceChangePeriod={1000}
              name="aceJsonEditor"
              editorProps={{$blockScrolling: true}}
              fontSize={14}
              showPrintMargin={true}
              showGutter={true}
              highlightActiveLine={true}
              value={defaultValue}
              width={this.props.width || '500px'}
              height={this.props.height || '500px'}
              wrapEnabled={true}
              enableBasicAutocompletion={false}
            />
  }
}